body {
  font-family: 'LatoWeb', sans-serif !important;
  background-color: #edeef0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'LatoWebBold' !important;
}
.text-center {
  text-align: center !important;
}
.text-left {
  text-align: left !important;
}
.text-right {
  text-align: right !important;
}
.ui.text.container {
  font-family: 'LatoWeb', sans-serif !important;
}
.ui.menu .item img.logo {
  margin-right: 1.5em;
}
.ui.footer.segment {
  margin: 5em 0em 0em;
  padding: 5em 0em;
}
.main.container {
  margin-top: 7em;
}
.wireframe {
  margin-top: 2em;
}
.profile-page .ui.cards .card {
  padding: 1em;
}
.profile-page .ui.cards .card .content {
  padding: 1em 0;
}
.profile-page .ui.cards .card .extra.content {
  padding: 1em 0 0 0;
}
.profile-page .profile-action-buttons {
  margin-top: 1em;
  text-align: center;
}
.feed-update-section {
  background-color: rgba(44, 138, 218, 0.22);
  padding: 0.7em 0;
  text-align: center;
  cursor: pointer;
  box-shadow: 0 1px 3px 0 #D4D4D5,0 0 0 1px #D4D4D5;
}
.ui.feed.has-updates {
  margin-top: 0.1em;
}
.ui.feed .event {
  padding: .6em;
  border-radius: 2px;
  background-color: #fff;
  margin-top: 1em;
  box-shadow: 0 1px 3px 0 #D4D4D5,0 0 0 1px #D4D4D5;
}
.ui.feed .event:first-child {
  padding-top: .6em;
  margin-top: 0;
}
.ui.feed .event:last-child {
  padding-bottom: .6em;
}
.ui.feed .event .content .extra.text {
  max-width: 100%;
}
.feed-page .feed-header {
  margin-bottom: 1.4em;
}
